@import "https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap";
html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  overflow-x: hidden;
}

div#main {
  flex-direction: column;
  width: 100%;
  display: flex;
}

section {
  width: 100%;
  position: relative;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  object-fit: cover;
  pointer-events: none;
  z-index: -1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

h1 {
  text-align: center;
  margin: 0;
  font-family: Cormorant Garamond, serif;
  font-size: 6rem;
  font-weight: 400;
  line-height: 1;
}

h2 {
  text-align: center;
  margin: 0;
  padding-top: 1rem;
  font-size: 2rem;
  font-weight: 400;
}

h3 {
  text-align: center;
  margin: 0;
  font-size: 1.25rem;
  font-weight: 400;
}

h3:hover {
  color: brown;
}

p {
  color: #fff5ee;
  text-align: center;
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
}

p.large {
  color: #fff5ee;
  font-size: 24px;
}

p.small {
  color: #272502;
  font-size: 15px;
  font-weight: 400;
}

hr {
  width: 80%;
  margin: 2.5rem auto;
}

li {
  color: #fff5ee;
  line-height: 1.7;
}

li::marker {
  color: #f5f5f5;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: #151d41;
}

.container {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 20px auto;
  padding: 0 15px;
  display: grid;
}

.container .box {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  display: flex;
}

img.socialIcon {
  width: 30px;
  height: 30px;
}

img.socialIcon:hover {
  filter: grayscale() brightness(0);
}

@media screen and (width >= 1200px) {
  h1 {
    font-size: 6rem;
  }

  h2 {
    font-size: 1.75rem;
  }

  .container {
    grid-template-columns: 1fr 1fr 1fr;
  }

  .container .box {
    padding: 28px 20px;
  }
}

@media screen and (width <= 1199px) and (width >= 768px) {
  h1 {
    font-size: 5rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  .container {
    grid-template-columns: 1fr 1fr;
  }

  .container .box {
    padding: 24px 18px;
  }

  p {
    font-size: 16px;
  }

  img.socialIcon {
    width: 25px;
    height: 25px;
  }
}

@media screen and (width <= 767px) and (width >= 480px) {
  h1 {
    font-size: 4rem;
  }

  h2 {
    font-size: 1.25rem;
  }

  .container {
    grid-template-columns: 1fr;
  }

  .container .box {
    padding: 20px 15px;
  }

  p {
    font-size: 15px;
  }

  img.socialIcon {
    width: 22px;
    height: 22px;
  }

  hr {
    width: 90%;
  }
}

@media screen and (width <= 479px) {
  h1 {
    text-align: left;
    font-size: 2rem;
  }

  h2 {
    text-align: left;
    font-size: 1.2rem;
  }

  p {
    font-size: 14px;
  }

  .container {
    grid-template-columns: 1fr;
    padding: 0 10px;
  }

  .container .box {
    padding: 18px 12px;
  }

  img.socialIcon {
    width: 20px;
    height: 20px;
  }

  hr {
    width: 95%;
  }

  ul {
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding-left: 20px;
    font-size: 16px;
    display: flex;
